import { createWebHistory, createRouter } from "vue-router";

import IndexComp from "../components/IndexComp";
import IntroComp from "../components/IntroComp";
import IntroBasic from "../components/IntroBasic";
import IntroMajor from "../components/IntroMajor";
import IntroSkills from "../components/IntroSkills";
import IntroSelf from "../components/IntroSelf";
import MainexpComp from "../components/MainexpComp";
import MainexpProject from "../components/MainexpProject";
import MainexpContest from "../components/MainexpContest";
import ExtraComp from "../components/ExtraComp";
import ExtraPart from "../components/ExtraPart";

const routes = [
  {
    path: "/",
    component: IndexComp,
  },
  {
    path: "/intro",
    component: IntroComp,
    children: [
      {
        path: "Basic",
        component: IntroBasic,
      },
      {
        path: "Major",
        component: IntroMajor,
      },
      {
        path: "Skills",
        component: IntroSkills,
      },
      {
        path: "SelfIntro",
        component: IntroSelf,
      },
    ],
  },
  {
    path: "/mainexp",
    component: MainexpComp,
    children: [
      {
        path: "project",
        component: MainexpProject,
      },
      {
        path: "contest",
        component: MainexpContest,
      },
    ],
  },
  {
    path: "/extra",
    component: ExtraComp,
    children: [
      {
        path: "parttime",
        component: ExtraPart,
      },
    ],
  },
];

const router = createRouter({
  // 라우트 생성
  history: createWebHistory(),
  routes,
});
export default router;
